import express from 'express';
import expressJWT from 'express-jwt';
import env from '../../../config/env';
import getLoggedInUser from '../../../middlewares/get_loggedIn_user';
import getHolograms from '../controllers/holograms_get';
import { baseUrl } from '../baseurl';

const router = express.Router();

/**
 * @swagger
 * /holograms:
 *   get:
 *     tags:
 *       - Holograms
 *     description: Returns list of Holograms
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "query"
 *         name: "search"
 *         description: "Search text"
 *         schema:
 *           type: object
 *           properties:
 *              search:
 *                  type: string
 *       - in: "query"
 *         name: "limit"
 *         description: "Sets the number of objects to be returned"
 *         schema:
 *           type: object
 *           properties:
 *              limit:
 *                  type: number
 *       - in: "query"
 *         name: "offset"
 *         description: "Sets the offset to the list of breakages"
 *         schema:
 *           type: object
 *           properties:
 *              offset:
 *                  type: number
 *       - in: "query"
 *         name: "from"
 *         description: "from date"
 *         schema:
 *           type: object
 *           properties:
 *              from:
 *                  type: date
 *       - in: "query"
 *         name: "to"
 *         description: "to date"
 *         schema:
 *           type: object
 *           properties:
 *              from:
 *                  type: date
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *          type: array
 *          items:
 *           type: object
 *           properties:
 *            _id:
 *              type: string
 *            spoolBarCode:
 *              type: string
 *            healEndCode:
 *              type: string
 *            purpose:
 *              type: string
 *            quantity:
 *              type: string
 *            healStartCode:
 *              type: string
 */

/**
 * @swagger
 * /holograms:
 *   post:
 *     tags:
 *       - Holograms
 *     description: Returns success message if Holograms details are added
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "body"
 *         description: "Required details"
 *         required: true
 *         schema:
 *           type: object
 *           properties:
 *            spoolBarCode:
 *              type: string
 *            healEndCode:
 *              type: string
 *            purpose:
 *              type: string
 *            quantity:
 *              type: string
 *            healStartCode:
 *              type: string
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *            _id:
 *              type: string
 *            spoolBarCode:
 *              type: string
 *            healEndCode:
 *              type: string
 *            purpose:
 *              type: string
 *            quantity:
 *              type: string
 *            healStartCode:
 *              type: string
 */

/**
 * @swagger
 * /holograms:
 *   put:
 *     tags:
 *       - Holograms
 *     description: Returns success message if Holograms details are updated
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "body"
 *         description: "Required details"
 *         required: true
 *         schema:
 *          type: array
 *          items:
 *            type: object
 *            properties:
 *              _id:
 *                type: string
 *              update:
 *                type: object
 *                properties:
 *                  spoolBarCode:
 *                    type: string
 *                  healEndCode:
 *                    type: string
 *                  purpose:
 *                    type: string
 *                  quantity:
 *                    type: string
 *                  healStartCode:
 *                    type: string
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *            message:
 *              type: string
 */

router.get(
  `${baseUrl}/`,
  expressJWT({
    secret: env.JWT_SECRET,
  }),
  getLoggedInUser,
  getHolograms
);

export default router;
